.center {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  .ring {
    position: absolute;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    animation: ring 2s linear infinite;

    @keyframes ring {
      0% {
        transform: rotate(0deg);
        box-shadow: 1px 5px 2px $color-6;
      }
      50% {
        transform: rotate(180deg);
        box-shadow: 1px 5px 2px $color-1;
      }
      100% {
        transform: rotate(360deg);
        box-shadow: 1px 5px 2px $color-7;
      }
    }
  }
  span {
    color: #737373;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 1px;
    line-height: 200px;
  }
  .ring::before {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    border-radius: 50%;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.3);
  }
}
